/* Global Styles */
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap");
@import "./themeStyles.css";

/* Reset and Base Styles */
* {
  box-sizing: border-box;
}

html {
  scroll-behavior: auto;
}

/* Smooth scrolling for anchor links only */
a[href^="#"] {
  scroll-behavior: smooth;
}

/* Ensure React Router links don't get smooth scrolling */
a[href^="/"] {
  scroll-behavior: auto;
}

body {
  margin: 0;
  padding: 0;
  font-family:
    "Inter",
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    "Roboto",
    sans-serif;
  background: var(--background-light);
  color: var(--text-color);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Add margin to main content areas */
.landing-futuristic,
.quiz-page,
.quizzes-page,
.privacy-policy-page {
  padding: 0 24px;
  max-width: 1200px;
  margin: 0 auto;
}

/* Responsive margins */
@media (max-width: 768px) {
  .landing-futuristic,
  .quiz-page,
  .quizzes-page,
  .privacy-policy-page {
    padding: 0 16px;
  }
}

@media (max-width: 480px) {
  .landing-futuristic,
  .quiz-page,
  .quizzes-page,
  .privacy-policy-page {
    padding: 0 12px;
  }
}

/* Button Styles */
.btn-minimal {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 16px 32px;
  font-size: 1rem;
  font-weight: 500;
  text-decoration: none;
  border: 2px solid transparent;
  border-radius: var(--border-radius-md);
  cursor: pointer;
  transition: all var(--transition-normal);
  background: transparent;
  color: var(--text-color);
  min-width: 160px;
  text-align: center;
}

.btn-minimal.primary {
  background: var(--gradient-primary);
  color: var(--text-white);
  border-color: var(--primary-color);
  box-shadow: var(--shadow-light);
}

.btn-minimal.primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-medium);
  border-color: var(--primary-dark);
}

.btn-minimal.secondary {
  background: transparent;
  color: var(--primary-color);
  border-color: var(--primary-color);
}

.btn-minimal.secondary:hover {
  background: var(--primary-color);
  color: var(--text-white);
  transform: translateY(-2px);
  box-shadow: var(--shadow-light);
}

/* Utility Classes */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

/* Focus Styles */
button:focus,
input:focus,
textarea:focus,
select:focus {
  outline: 2px solid var(--primary-color);
  outline-offset: 2px;
}

/* Smooth Transitions */
* {
  transition:
    color 0.3s ease,
    background-color 0.3s ease,
    border-color 0.3s ease;
}
