/* Security Components Styles */

/* Secure Input Styles */
.secure-input-container {
  position: relative;
  margin-bottom: 16px;
}

.secure-input {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  background: var(--glass-bg);
  color: var(--text-color);
  font-size: 16px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.secure-input:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(6, 182, 212, 0.1);
}

.secure-input.error {
  border-color: #ef4444;
  box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.1);
}

.secure-input::placeholder {
  color: var(--text-muted);
}

.input-error {
  color: #ef4444;
  font-size: 14px;
  margin-top: 4px;
  padding: 4px 8px;
  background: rgba(239, 68, 68, 0.1);
  border-radius: 4px;
  border-left: 3px solid #ef4444;
}

.input-counter {
  position: absolute;
  bottom: -20px;
  right: 0;
  font-size: 12px;
  color: var(--text-muted);
}

/* Security Audit Styles */
.security-audit {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
}

.security-audit-toggle {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: var(--glass-bg);
  border: 1px solid var(--border-color);
  color: var(--text-color);
  font-size: 18px;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.security-audit-toggle:hover {
  background: var(--primary-color);
  color: white;
  transform: scale(1.1);
}

.security-audit-panel {
  position: absolute;
  bottom: 60px;
  right: 0;
  width: 400px;
  max-height: 500px;
  background: var(--glass-bg);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  padding: 20px;
  backdrop-filter: blur(20px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
}

.security-audit-panel h3 {
  margin: 0 0 16px 0;
  color: var(--text-color);
  font-size: 18px;
  font-weight: 600;
}

.security-issues {
  margin-bottom: 16px;
}

.security-issue {
  padding: 12px;
  margin-bottom: 8px;
  border-radius: 8px;
  border-left: 4px solid;
}

.security-issue.error {
  background: rgba(239, 68, 68, 0.1);
  border-left-color: #ef4444;
}

.security-issue.warning {
  background: rgba(245, 158, 11, 0.1);
  border-left-color: #f59e0b;
}

.security-issue.info {
  background: rgba(59, 130, 246, 0.1);
  border-left-color: #3b82f6;
}

.issue-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.issue-type {
  font-weight: 600;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.security-issue.error .issue-type {
  color: #ef4444;
}

.security-issue.warning .issue-type {
  color: #f59e0b;
}

.security-issue.info .issue-type {
  color: #3b82f6;
}

.issue-context {
  font-size: 12px;
  color: var(--text-muted);
  background: rgba(0, 0, 0, 0.1);
  padding: 2px 6px;
  border-radius: 4px;
}

.issue-message {
  margin: 0 0 8px 0;
  font-size: 14px;
  line-height: 1.4;
  color: var(--text-color);
}

.issue-timestamp {
  color: var(--text-muted);
  font-size: 11px;
}

/* Rate Limit Styles */
.rate-limit-warning {
  background: rgba(245, 158, 11, 0.1);
  border: 1px solid #f59e0b;
  border-radius: 8px;
  padding: 12px;
  margin: 16px 0;
  color: var(--text-color);
}

.rate-limit-error {
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid #ef4444;
  border-radius: 8px;
  padding: 12px;
  margin: 16px 0;
  color: var(--text-color);
}

.rate-limit-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
}

.rate-limit-progress {
  width: 100%;
  height: 4px;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  margin-top: 8px;
  overflow: hidden;
}

.rate-limit-progress-bar {
  height: 100%;
  background: var(--primary-color);
  transition: width 0.3s ease;
}

/* Security Headers Info */
.security-headers-info {
  background: rgba(34, 197, 94, 0.1);
  border: 1px solid #22c55e;
  border-radius: 8px;
  padding: 12px;
  margin: 16px 0;
  color: var(--text-color);
  font-size: 14px;
}

.security-headers-info h4 {
  margin: 0 0 8px 0;
  color: #22c55e;
  font-size: 16px;
}

.security-headers-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.security-headers-list li {
  padding: 4px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.security-headers-list .header-name {
  font-weight: 600;
  color: var(--text-color);
}

.security-headers-list .header-status {
  font-size: 12px;
  padding: 2px 6px;
  border-radius: 4px;
  background: rgba(34, 197, 94, 0.2);
  color: #22c55e;
}

/* Responsive Design */
@media (max-width: 768px) {
  .security-audit-panel {
    width: 300px;
    right: -50px;
  }

  .security-audit-toggle {
    width: 45px;
    height: 45px;
    font-size: 16px;
  }
}
